<template>
  <ion-page>
    <ion-header>
      <ion-toolbar>
        <ion-title>Test Details</ion-title>
      </ion-toolbar>
    </ion-header>
    <ion-content>
      <ion-grid>
        <ion-row>
          <ion-col size="6">
            <ion-card>
              <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600"
                  >Test Type</ion-label
                >
                <ion-label slot="end">{{ details.test_type_name }}</ion-label>
              </ion-item>
              <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600"
                  >Date Ordered</ion-label
                >
                <ion-label slot="end">{{
                  moment(details.created_date).format("DD/MMMM/YYYY HH:mm")
                }}</ion-label>
              </ion-item>
              <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600"
                  >Receipt Date</ion-label
                >
                <ion-label slot="end">{{
                  receiptDate != ""
                    ? moment(receiptDate).format("DD/MMMM/YYYY HH:mm")
                    : ""
                }}</ion-label>
              </ion-item>
              <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600"
                  >Test Status</ion-label
                >
                <ion-label slot="end">{{ details.status }}</ion-label>
              </ion-item>
              <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600"
                  >Request Origin</ion-label
                >
                <ion-label slot="end">{{ details.request_origin }}</ion-label>
              </ion-item>
              <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600"
                  >Requesting Ward/Location</ion-label
                >
                <ion-label slot="end">{{ details.requesting_ward }}</ion-label>
              </ion-item>
              <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600"
                  >Requesting Physician</ion-label
                >
                <ion-label slot="end">{{ details.requested_by }}</ion-label>
              </ion-item>
              <ion-item>
                <ion-label style="font-size: 18px; font-weight: 600"
                  >Registered By</ion-label
                >
                <ion-label slot="end">{{ requestedBy }}</ion-label>
              </ion-item>
            </ion-card>
          </ion-col>
          <ion-col size="6">
            <ion-card>
              <ion-toolbar color="primary">
                <ion-title> Patient Details </ion-title>
              </ion-toolbar>
              <ion-list>
                <ion-item>
                  <ion-label style="font-size: 18px; font-weight: 600"
                    >Patient Identifier</ion-label
                  >
                  <ion-label slot="end">{{ details?.client.id }}</ion-label>
                </ion-item>
                <ion-item>
                  <ion-label style="font-size: 18px; font-weight: 600"
                    >Patient Name</ion-label
                  >
                  <ion-label slot="end">
                    {{
                      `${details.first_name} ${
                        details.middle_name !== null ? details.middle_name : ""
                      } ${details.last_name}`
                    }}
                  </ion-label>
                </ion-item>
                <ion-item>
                  <ion-label style="font-size: 18px; font-weight: 600"
                    >Age</ion-label
                  >
                  <ion-label slot="end">{{
                    moment(details?.client.date_of_birth).from(new Date())
                  }}</ion-label>
                </ion-item>
                <ion-item>
                  <ion-label style="font-size: 18px; font-weight: 600"
                    >Sex</ion-label
                  >
                  <ion-label slot="end">{{ details?.client.sex }}</ion-label>
                </ion-item>
              </ion-list>
            </ion-card>

            <ion-card>
              <ion-toolbar color="primary">
                <ion-title> Specimen Details </ion-title>
              </ion-toolbar>
              <ion-list>
                <ion-item>
                  <ion-label style="font-size: 18px; font-weight: 600"
                    >Type</ion-label
                  >
                  <ion-label slot="end">{{ details.specimen_type }}</ion-label>
                </ion-item>
                <ion-item>
                  <ion-label style="font-size: 18px; font-weight: 600"
                    >Tracking Number</ion-label
                  >
                  <ion-label slot="end">{{
                    details.tracking_number
                  }}</ion-label>
                </ion-item>
                <ion-item>
                  <ion-label style="font-size: 18px; font-weight: 600"
                    >Accession Number</ion-label
                  >
                  <ion-label slot="end">{{
                    details.accession_number
                  }}</ion-label>
                </ion-item>
                <ion-item>
                  <ion-label style="font-size: 18px; font-weight: 600"
                    >Status</ion-label
                  >
                  <ion-label slot="end">{{ details.order_status }}</ion-label>
                </ion-item>
              </ion-list>
            </ion-card>
          </ion-col>
        </ion-row>
      </ion-grid>
    </ion-content>
    <ion-footer>
      <ion-toolbar style="padding: 10px">
        <ion-button size="large" color="success" slot="start" @click="goBack">
          Back
        </ion-button>

        <ion-button size="large" slot="end" @click="navigateToPatient">
          View Patient
        </ion-button>
        <ion-button size="large" slot="end" @click="printAccessNumber">
          Print Accession Number
        </ion-button>
      </ion-toolbar>
    </ion-footer>
  </ion-page>
</template>

<script lang="ts">
import {
  IonPage,
  IonContent,
  IonToolbar,
  IonHeader,
  IonTitle,
  IonFooter,
  IonButton,
  IonCol,
  IonRow,
  IonGrid,
  IonCard,
  IonItem,
  IonLabel,
  IonList,
} from "@ionic/vue";
import { Client } from "@/composables/client";
import { defineComponent } from "vue";
import moment from "moment";
import PrintService from "@/services/print_service";
import router from "@/router";

export default defineComponent({
  components: {
    IonPage,
    IonContent,
    IonToolbar,
    IonHeader,
    IonTitle,
    IonFooter,
    IonButton,
    IonCol,
    IonRow,
    IonGrid,
    IonCard,
    IonItem,
    IonLabel,
    IonList,
  },
  data() {
    return {
      details: {
        client: {
          first_name: "",
          middle_name: "",
          last_name: "",
          date_of_birth: "",
          sex: "",
        },
      } as any,
      requestedBy: "",
      receiptDate: "",
      moment: moment,
    };
  },
  async mounted() {
    const client = Client();

    const response = await client.getJson(
      `tests/${this.$router.currentRoute.value.query.test_id?.toString()}`
    );

    if (response?.ok) {
      this.details = response?.data;

      this.details.status_trail.map((trail: any) => {
        if (trail.status.name == "pending") {
          this.requestedBy = `${trail.initiator.first_name} ${trail.initiator.last_name}`;
        } else if (trail.status.name == "accepted") {
          this.receiptDate = trail.created_date;
        }
      });

      this.details.order_status_trail.map((trail: any) => {
        if (trail.status.name == "specimen-accepted") {
          this.receiptDate = trail.created_date;
        }
      });
    } else {
      console.log(response?.error);
    }
  },
  methods: {
    goBack() {
      router.back();
    },
    navigateToPatient() {
      router.push(`/patient-details/${this.details.client.id}`);
    },
    printAccessNumber() {
      const printService = new PrintService();

      const accession_number = this.details.accession_number;

      printService.writeLbl(
        "printout/accession_number",
        { accession_number },
        `${accession_number}.lbl`
      );
    },
  },
});
</script>
